import React from 'react';
import ContactForm from '../ContactFrom'
import cs1 from '../../images/man-sending-message.jpg'
import './style.css'

const Contactpage = () => {

    return(
        <section className="wpo-contact-form-map section-padding">
            <div className="hx-contact-area-s2 ptb-100-70">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-5">
                            <div className="hx-contact-img">
                                <img src={cs1} alt=""/>
                            </div>
                        </div>
                        <div className="col col-lg-7 col-md-12 col-sm-12">
                            <div className="hx-contact-content">
                                <h2>Laissez-nous un message</h2>
                                <div className="hx-contact-form">
                                    <ContactForm/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="hx-contact-grid-area hx-section-padding">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-4 col-md-6 col-sm-12 col-12">
                            <div className="hx-contact-gd-wrap">
                                <div className="hx-contact-gd-icon">
                                    <i className="fi flaticon-call"></i>
                                </div>
                                <div className="hx-contact-gd-text">
                                    <h4>Vous pouvez nous appeler</h4>
                                    <span>+(033) 07 51 30 47 04 </span>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-6 col-sm-12 col-12">
                            <div className="hx-contact-gd-wrap">
                                <div className="hx-contact-gd-icon">
                                    <i className="fi flaticon-message"></i>
                                </div>
                                <div className="hx-contact-gd-text">
                                    <h4>Envoyez-nous un email</h4>
                                    <span>info@adamoautos.com</span>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-6 col-sm-12 col-12">
                            <div className="hx-contact-gd-wrap">
                                <div className="hx-contact-gd-icon">
                                    <i className="fi flaticon-placeholder"></i>
                                </div>
                                <div className="hx-contact-gd-text">
                                    <h4>Localisation</h4>
                                    <span>14 chemin des closeaux, 94440 Villecresnes</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col col-xs-12">
                            {/* <div className="contact-map">
                                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d193595.9147703055!2d-74.11976314309273!3d40.69740344223377!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89c24fa5d33f083b%3A0xc80b8f06e177fe62!2sNew+York%2C+NY%2C+USA!5e0!3m2!1sen!2sbd!4v1547528325671"></iframe>
                            </div>                         */}
                        </div>
                    </div>
                </div>
            </div>
        </section>
     )
        
}

export default Contactpage;
